/* Customize your main colors in :root variables */
:root {
  --main-background-color: #333333;
  --card-background-color: #333333;
  --countdown-background-color: #ca4c62;
  --main-text-color:#F7F6F4;
  --title-text-color:#e3a9bc;
  --home-text-color:#e3a9bc;
}

body{
 background-color:var(--main-background-color);
  background: url("img/background2.png");
  background-size: cover;
  background-attachment: fixed;
  font-family: 'Changa One', 'VT323', monospace, 'Signika', sans-serif;
  font-size: 1.5em; 
/* filter: blur(20px);*/
}

h1, h2, h3, p, em{
  color: var(--home-text-color);
}

h3{
font-weight: normal;
}

p, em{
  font-family: 'Changa', sans-serif;
}



